body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eeeeee; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.container-loader {
  text-align: center;
  align-items: center; }
  .container-loader .pointer {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 10%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 300px;
    height: 300px;
    background-color: #fff;
    border-radius: 50%;
    -webkit-transition: 0.000s ease-in;
    transition: 0.000s ease-in;
    z-index: 6;
    mix-blend-mode: difference; }
  .container-loader .thumb {
    background-color: #eeeeee; }
    .container-loader .thumb .container-post {
      justify-content: center;
      align-content: center;
      display: inline-grid; }
      .container-loader .thumb .container-post .main-cont {
        padding-top: 1em; }
      .container-loader .thumb .container-post .inline-class {
        display: inline-flex;
        justify-content: center;
        align-items: center; }
      .container-loader .thumb .container-post .left-side {
        text-align: left;
        margin: 1em; }
      .container-loader .thumb .container-post .charts-container {
        margin: 1em; }
      .container-loader .thumb .container-post .title {
        font-size: 42px;
        margin: 1em;
        height: 30%;
        width: 100%; }
        @media screen and (max-width: 765px) {
          .container-loader .thumb .container-post .title {
            font-size: 32px; } }
      .container-loader .thumb .container-post .container-title {
        height: 100px; }
        .container-loader .thumb .container-post .container-title .title {
          font-size: 42px;
          margin: 1em;
          height: 30%;
          width: 100%; }
      .container-loader .thumb .container-post .container-info-main {
        display: inline-flex;
        justify-content: center; }
        .container-loader .thumb .container-post .container-info-main .container-info {
          height: auto;
          width: 600px; }

.link-to {
  padding: 0;
  color: #424242; }

.col-main {
  text-align: center; }
  .col-main:hover .span-name-main {
    color: red; }

.menu-div {
  height: 65px; }
  .menu-div .text-to-small-smartphone {
    display: none; }
  @media screen and (max-width: 765px) {
    .menu-div {
      background-color: transparent; }
      .menu-div .text-to-small-smartphone {
        display: block;
        justify-content: center;
        display: flex;
        font-size: 200%;
        font-weight: 600;
        margin-top: 4em; }
      .menu-div .navbar {
        display: flex;
        justify-content: center; }
        .menu-div .navbar .navbar-nav {
          display: flex;
          padding: 0; }
      .menu-div .navbar-toggler {
        height: 100%;
        width: 20%;
        background: #82ca9d; } }

.container-main-own .row-main-own {
  display: flex; }
  .container-main-own .row-main-own .col-home {
    width: 30%;
    margin: 2em; }

@media screen and (max-width: 1200px) {
  .container-main-own {
    width: 1200px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    align-self: center; }
    .container-main-own .row-main-own {
      display: grid;
      justify-content: center;
      width: inherit; } }

@media screen and (max-width: 765px) {
  .container-main-own {
    display: grid;
    width: 100%; }
    .container-main-own .row-main-own {
      display: grid;
      width: 100%;
      padding: 0; }
      .container-main-own .row-main-own .col-home {
        width: 100%;
        margin: 0; }
        .container-main-own .row-main-own .col-home .link-to img {
          width: 100%;
          padding: 1rem; } }

.content-main-own {
  margin: 1em;
  padding: 3em;
  display: flex;
  justify-content: center;
  height: 100vh; }
  @media screen and (max-width: 765px) {
    .content-main-own {
      padding: 2em;
      margin: 0;
      height: 100%; } }

.container-home {
  width: 100%; }

.footer-container {
  text-align: center;
  padding: 2em 0 2em 0;
  width: auto;
  color: darkred; }

.loading {
  display: flex;
  justify-content: center; }

.loader {
  font-size: 42px;
  padding: 3rem; }

